import React, {useState} from "react";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Forms from '../components/Forms';
import banner from '../assets/img/banner.png';
import bg from '../assets/img/bg_banner.png';
import bg2 from '../assets/img/bg_bieu-phi.png';
import bg3 from '../assets/img/test2.jpg';
import bg33 from '../assets/img/test2_mb.jpg';
import bg4 from '../assets/img/test3.png';
import bg5 from '../assets/img/bg_banner_mb.png';
import {Container} from "react-bootstrap";
import SimpleSlider from "../components/SimpleSlider";
import QlSlider from "../components/QlSlider";
import BpSlider from "../components/BpSlider";
import ProcedureSlider from "../components/ProcedureSlider";
import MediaQuery, {useMediaQuery} from 'react-responsive'
import ThuTuc from "../components/ThuTuc";
import HoiDap from "../components/HoiDap";
import b1 from "../assets/img/1.png"
import b2 from "../assets/img/2.png"
import b3 from "../assets/img/3.png"
import b4 from "../assets/img/4.png"
import {Tabs, Tab, Panel} from '@bumaga/tabs'


function ItemBox(proj) {
    return (
        <div className="item">
            <div className="box">
                <img src={proj.image} alt=""/>
                <span>{proj.name}</span>
                <div className="txt_1">
                    {proj.keyword}
                </div>
            </div>
        </div>
    )
}

function ItemList(proj) {
    return (
        <div className="d-flex flex-row align-items-center">
            <div className="txt-num">{proj.name}</div>
            <div className="txt-name ml-3">
                <div className="title_txt">{proj.description}</div>
                <div className="desc_txt">{proj.content}</div>
            </div>
        </div>
    )
}

function QuyTrinhList(proj) {
    return (
        <div className="item">
            <div className="icon">
                <img src={proj.image} alt="BAO VIET"/>
                <div className="step">
                    {proj.name}
                </div>
            </div>
            <div className="steps">
                <div className="desc">
                    {proj.keyword}
                </div>
            </div>
        </div>

    )
}

function Trangchu() {
    const [tabIndex, setTabIndex] = useState(0);
    const tabIndexData = [
        'Quy trnh bo lnh',
        'Quy trnh bi thng',
    ]
    const currentUrl = window.location.href;
    const quytrinhData = [
        {image:`${currentUrl}/img/bb1.png`, name:'Bc 1', keyword:'Xut trnh CMND v th BH.'},
        {image:`${currentUrl}/img/bb2.png`, name:'Bc 2', keyword:'Tin hnh khm cha bnh'},
        {image:`${currentUrl}/img/bb3.png`, name:'Bc 3', keyword:'K giy bo lnh / nhn li tin t cc nu c/ thanh ton cc chi ph khng c chi tr.'},
    ]
    const quytrinhboithuongData = [
        {image:`${currentUrl}/img/1.png`, name:'Bc 1', keyword:'Khm, nhp vin / iu tr theo ch nh ca bc s.'},
        {image:`${currentUrl}/img/2.png`, name:'Bc 2', keyword:'Thanh ton cc chi ph cho bnh vin. Ly cc chng t y t, ha n.'},
        {image:`${currentUrl}/img/3.png`, name:'Bc 3', keyword:'Gi h s n Bo Vit (trong 60 ngy sau ngy iu tr cui cng/ t vong).'},
        {image:`${currentUrl}/img/4.png`, name:'Bc 4', keyword:'Bo Vit tip nhn h s bi thng.'},
        {image:`${currentUrl}/img/5.png`, name:'Bc 5', keyword:'Thng bo v tnh trng h s bi thng (3 ngy lm vic sau khi nhn  h s).'},
        {image:`${currentUrl}/img/6.png`, name:'Bc 6', keyword:'Bo Vit hon tt thm nh h s v hon tr kt qu trong vng 15 ngy.'},
    ]
    const isMobile = useMediaQuery({query: '(max-width: 991px)'})
    return (
        <>
            <div className="hero_banner"
                 style={{
                     backgroundImage: `url(${isMobile ? bg5 : bg})`,
                 }}
            >
                <div className="nhut_style" id="dang-ky-tu-van">
                    <Container className="sec-container">
                        <Row className="wp no-gutters">
                            <div className="col-custom-left">
                                <div className="box-card">
                                    <img className="pc"
                                         src={banner}/>
                                    <img className="mb"
                                         src={banner}/>
                                </div>
                            </div>
                            <div className="col-custom-right">
                                <div className="wp_form">
                                    <div className="h5 wap_form_title">Nhp thng tin  c t vn min ph</div>
                                    <div className="fs-14 text-center mb-2"><span
                                        className="text-danger">Lu :</span> Tr em di 6 tui cn mua km 1 ngi ln
                                    </div>
                                    <Forms/>
                                </div>
                            </div>
                        </Row>
                    </Container>

                </div>
            </div>

            <div id="gioi-thieu" className="sec section_moi nhut_style">
                <Container>
                    <div className="div_title">
                        <h4 className="title_tt">Bo him sc khe Bo Vit An Gia<p>An tm tn hng cuc sng</p></h4>
                        <div className="title_txt">
                            <p>Cuc sng lun trn ngp nhng iu bt ng. Ch ng bo v sc khe vi bo him Bo
                                Vit An Gia chnh l gii php gip ti u quyn li y t, mang n s an tm vui sng
                                cho bn v gia nh.</p></div>
                    </div>
                    <div className="div_video">
                        <SimpleSlider isMobile={isMobile}/>
                    </div>
                </Container>
            </div>

            <div id="quyen-loi" className="sec sec_ql">
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Quyn li</h4>
                            <div className="title_txt">
                                <p>Dnh cho mi cng dn v ngi nc ngoi t 15 ngy tui n 60 tui.</p></div>
                        </div>
                        <div className="div_quyenloi">
                            <div className="ql_slider style_dot style_arrow ql_no_slider ql_no_slider_center">
                                <QlSlider></QlSlider>
                            </div>
                        </div>
                    </Container>
                </div>
            </div>

            <div id="bieu-phi" className="sec sec_bp"
                 style={{
                     backgroundImage: "url(" + bg2 + ")",
                 }}
            >

                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className=" title_tt">Biu ph bo him</h4>
                        </div>
                        <div className="div_bieuphi">

                            <BpSlider isMobile={isMobile}/>

                        </div>
                    </Container>

                    <Container className="con-promo">
                        <div className="ql_promo"
                             style={{
                                 '--bs-bg-m': `url(${isMobile ? bg33 : bg3})`,
                                 '--bs-bg': `url(${isMobile ? bg33 : bg3})`
                             }}
                        >
                            <div className="ql_tuvan">
                                <div className="ql_tuvan_div">
                                    <div className="ql_tuvan_tt">u i c bit</div>
                                    <div className="ql_tuvan_txt"><p>Khi mua theo nhm</p></div>
                                    <a className="btn_web" href="#!"><span>ng k t vn - Min ph!</span>
                                        &nbsp;
                                        <i className="fas fa-arrow-right"></i>
                                    </a>

                                </div>
                            </div>
                        </div>
                    </Container>
                </div>


            </div>

            <div id="uu-diem" className="sec sec_rules"
                 style={{
                     '--bs-bg-m': `url(${bg4})`,
                     '--bs-bg': `url(${bg4})`
                 }}
            >
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Th tc nhanh chng</h4>
                        </div>
                        <div className="div_rules">
                            {isMobile ? (
                                <ThuTuc/>
                            ) : (
                                <div className="rules_dt style_dot style_arrow d-flex flex-row justify-content-center">
                                    <ItemBox
                                        image={b1}
                                        name="200+"
                                        keyword="Bnh vin cht lng cao trn c nc"
                                    />
                                    <ItemBox
                                        image={b2}
                                        name="24/7"
                                        keyword="Dch v bo lnh vin ph u vit"
                                    />
                                    <ItemBox
                                        image={b3}
                                        name="0"
                                        keyword="Khng yu cu khm sc khe trc khi ng k"
                                    />
                                    <ItemBox
                                        image={b4}
                                        name="0"
                                        keyword="Khng mt thi gian thu thp h s yu cu bi thng"
                                    />
                                </div>
                            )}
                            <div className="wp-btn-tuvan">
                                <a className="btn_web btn_center txt_1" href="#!"><span>T vn min ph</span></a>
                            </div>
                        </div>
                    </Container>
                </div>
            </div>

            <div id="hoi-dap" className="sec sec_tai_sao">
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Ti sao chn Bo Vit An Gia?</h4>
                        </div>
                        <div className="div_taisao">
                            {isMobile ? (
                                <HoiDap/>
                            ) : (
                                <div className="wp d-lg-flex flex-row justify-content-center align-items-center">
                                    <div className="wp-form left">
                                        <ItemList
                                            name="1"
                                            description="m bo ti chnh vng vng trc ri ro."
                                            content="Gim bt p lc ti chnh khi bin c xy ra, gip bn an tm iu tr."
                                        />
                                        <ItemList
                                            name="2"
                                            description="Dch v bo lnh vin ph rng khp Vit Nam."
                                            content="Ty chn bnh vin t nhn v quc t trn ton quc m bn tin tng."
                                        />
                                        <ItemList
                                            name="3"
                                            description="Mua nhanh n gin, khng cn khm sc khe."
                                            content="ng k nhanh gn ch trong vng 5 pht vi ph ch t 3.000 ng/ngy."
                                        />
                                    </div>
                                    <div className="wp-form right">
                                        <div className="procedure_slider">
                                            <div data-slider="1"
                                                 className="procedure_for style_arrow slick-initialized slick-slider slick-dotted">
                                                <ProcedureSlider></ProcedureSlider>
                                            </div>
                                            <div className="procedure_nav">

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                            }
                        </div>
                    </Container>
                </div>
            </div>
            <div id="quy-trinh" className="sec sec_quy_trinh">
                <div className="sec">
                    <Container>
                        <div className="div_title">
                            <h4 className="title_tt">Quy trnh n gin</h4>

                        </div>
                        <div className="div_quytrinh">
                            <Tabs>
                                <div className="div_fixedElement">
                                    <ul className="fixedElement nav nav-tabs quick_tabs_btn btn_mask procedure_btn justify-content-center d-flex flex-wrap">
                                        {tabIndexData.map((item, index) =>
                                            <Tab>
                                                <li className="nav-item">
                                                    <a onClick={() => setTabIndex(index)}
                                                       className={`nav-link ${tabIndex == index ? 'active' : ''}`}>{item}</a>
                                                </li>
                                            </Tab>)}
                                    </ul>
                                </div>

                                <Panel>
                                    <div className="procedure_note">
                                        Bo Vit s thanh ton chi ph theo ni dung bo him, ngi c bo him
                                        khng cn thanh ton. Quy trnh bo lnh nh sau (Khi khm/ iu tr trong
                                        h thng bo lnh):
                                    </div>

                                    <div
                                        className="procedure_slider_list section_moi_slider style_dot style_arrow">
                                        {
                                            quytrinhData.map((data, index)=>{
                                                return (
                                                    <QuyTrinhList
                                                        image={data.image}
                                                        name={data.name}
                                                        keyword={data.keyword}
                                                    />
                                                )
                                            })
                                        }
                                    </div>
                                </Panel>
                                <Panel>
                                    <div className="procedure_note">
                                        Ngi c bo him thanh ton trc, Bo Vit thanh ton chi ph thuc ni
                                        dung bo him sau. Quy trnh bi thng nh sau (Khi khm/ iu tr ngoi h
                                        thng bo lnh):
                                    </div>
                                    <div className="procedure_slider_list">
                                        {
                                            quytrinhboithuongData.map((data, index)=>{
                                                return (
                                                    <QuyTrinhList
                                                        image={data.image}
                                                        name={data.name}
                                                        keyword={data.keyword}
                                                    />
                                                )
                                            })
                                        }
                                    </div>
                                </Panel>
                            </Tabs>

                        </div>
                    </Container>
                </div>
            </div>


        </>


    )
}


export default Trangchu;